body {
  font-family: "Pontano Sans";
  
}
.page-headings {
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
}

.board {
  text-align: center;
}

.leader-board {
  margin: 40px;
  font-weight: bold;
  color: rgb(4, 52, 71);
}

.heading-backtest {
  margin: 15px;
  font-weight: bold;
  color: rgb(4, 52, 71);
  display: inline-block;
}

.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.change-slippage {
  display: flex;
}

.selector {
  border: 1px solid grey;
  border-radius: 8px;
}

.headings {
  margin: 6px;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.first {
  background-color: goldenrod;
}
.second {
  background-color: silver;
}
.third {
  background-color: saddlebrown;
}

.ranking {
  display: grid;
  grid-template-columns: 0px 135px 100px 120px 120px 100px 80px 50px;
  position: relative;
  flex-direction: row;
  justify-content: space-between;
}

.calmar-ratio-heading {
  margin-left: 35px;
} 

.calmar-ratio {
  padding-right: 15px;
}